<template>
  <v-container class="max-auto justify-center">
    <v-layout>
      <br />

      <!-- 검색 -->
      <v-row class="text-center">
        <v-col cols="12" md="2">
          <v-select label="검색조건">검색 </v-select>
        </v-col>
        <!-- <div>등록일, 수정일, 카테고리, inputbox, 검색 버튼</div> -->
      </v-row>
    </v-layout>

    <div>
      <div>총 건</div>
      <table class="table">
        <tr>
          <th>카테고리</th>
          <th>제목</th>
          <th>작성자</th>
          <th>조회수</th>
          <th>등록일시</th>
          <th>수정일시</th>
        </tr>
        <tr v-for="board in boards" v-bind:key="board.boardId">
          <td>{{ board.category }}</td>
          <td>
            <a @click="clickDetail(board.boardId)">{{ board.title }}</a>
          </td>
          <td>{{ board.userName }}</td>
          <td>{{ board.viewCount }}</td>
          <td>{{ board.createDate }}</td>
          <td>{{ board.modifyDate }}</td>
        </tr>
        <tr v-if="boards.length == 0">
          <td>데이터가 없습니다.</td>
        </tr>
      </table>
    </div>
    <!-- 페이지네이션 -->
    <div></div>
    <!-- 버튼 -->
    <div class="buttons">
      <button>목록</button>
      <button @click="register('/write')">등록</button>
    </div>
  </v-container>
</template>

<script>
import BoardService from "@/service/BoardSevice";

export default {
  name: "Boards",
  data() {
    return {
      boards: [],
    };
  },
  methods: {
    getBoardList() {
      BoardService.getBoardsAPI()
        .then((response) => {
          this.boards = response.data;
          console.log(response.data);
        })
        .catch((e) => {
          console.log(e);
        });
    },

    /* TODO 220509 boardId undefined */
    clickDetail(boardId) {
      this.$router.push(`${boardId}`);
    },
  },
  mounted() {
    this.getBoardList();
  },
};
</script>

<style scoped>
.board table td:nth-child(1) span {
  cursor: pointer;
}

a {
  cursor: pointer;
}
</style>
